div {
	&.DTE {
	  position: relative;
  
	  div {
		&.DTE_Header {
		  position: absolute;
		  top: 0;
		  left: 0;
		  height: 52px;
		  width: 100%;
		  background-color: #e8e9eb;
		  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
		  padding: 16px 10px 2px 16px;
		  font-size: 1.3em;
		  -webkit-box-sizing: border-box;
		  -moz-box-sizing: border-box;
		  box-sizing: border-box;
		}
  
		&.DTE_Body {
		  position: relative;
		}
  
		&.DTE_Footer {
		  position: absolute;
		  bottom: 0;
		  left: 0;
		  height: 52px;
		  width: 100%;
		  background-color: #e8e9eb;
		  border-top: 1px solid rgba(0, 0, 0, 0.15);
		  padding: 10px;
		  -webkit-box-sizing: border-box;
		  -moz-box-sizing: border-box;
		  box-sizing: border-box;
		}
  
		&.DTE_Form_Info {
		  margin-bottom: .5em;
		  display: none;
		}
  
		&.DTE_Form_Content {
		  position: relative;
		  padding: 10px;
		}
  
		&.DTE_Form_Error {
		  float: left;
		  padding: 5px;
		  display: none;
		  color: #b11f1f;
		  white-space: nowrap;
		  overflow: hidden;
		  text-overflow: ellipsis;
		}
	  }
  
	  button.btn, div.DTE_Form_Buttons button {
		position: relative;
		text-align: center;
		display: block;
		margin-top: 0;
		padding: .5em 1em;
		cursor: pointer;
		float: right;
		margin-left: .75em;
		color: inherit;
		font-size: 14px;
		white-space: nowrap;
		border: 1px solid #999;
		-webkit-border-radius: 2px;
		-moz-border-radius: 2px;
		-ms-border-radius: 2px;
		-o-border-radius: 2px;
		border-radius: 2px;
		-webkit-box-shadow: 1px 1px 3px #ccc;
		-moz-box-shadow: 1px 1px 3px #ccc;
		box-shadow: 1px 1px 3px #ccc;
		background-color: #dcdcdc;
		background-image: -webkit-linear-gradient(top, $white 0%, #dcdcdc 100%);
		background-image: -moz-linear-gradient(top, $white 0%, #dcdcdc 100%);
		background-image: -ms-linear-gradient(top, $white 0%, #dcdcdc 100%);
		background-image: -o-linear-gradient(top, $white 0%, #dcdcdc 100%);
		background-image: linear-gradient(to bottom, $white 0%, #dcdcdc 100%);
		filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr="$white", EndColorStr="#dcdcdc");
	  }
  
	  button.btn:hover, div.DTE_Form_Buttons button:hover {
		border: 1px solid #666;
		-webkit-box-shadow: 1px 1px 3px #999;
		-moz-box-shadow: 1px 1px 3px #999;
		box-shadow: 1px 1px 3px #999;
		background-color: #ccc;
		background-image: -webkit-linear-gradient(top, #eaeaea 0%, #cccccc 100%);
		background-image: -moz-linear-gradient(top, #eaeaea 0%, #cccccc 100%);
		background-image: -ms-linear-gradient(top, #eaeaea 0%, #cccccc 100%);
		background-image: -o-linear-gradient(top, #eaeaea 0%, #cccccc 100%);
		background-image: linear-gradient(to bottom, #eaeaea 0%, #cccccc 100%);
		filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr="#eaeaea", EndColorStr="#cccccc");
	  }
  
	  button.btn:active, div.DTE_Form_Buttons button:active {
		-webkit-box-shadow: inset 1px 1px 3px #999;
		-moz-box-shadow: inset 1px 1px 3px #999;
		box-shadow: inset 1px 1px 3px #999;
	  }
  
	  button.btn:focus, div.DTE_Form_Buttons button:focus {
		border: 1px solid #426c9e;
		text-shadow: 0 1px 0 #c4def1;
		background-color: #79ace9;
		background-image: -webkit-linear-gradient(top, #bddef4 0%, #79ace9 100%);
		background-image: -moz-linear-gradient(top, #bddef4 0%, #79ace9 100%);
		background-image: -ms-linear-gradient(top, #bddef4 0%, #79ace9 100%);
		background-image: -o-linear-gradient(top, #bddef4 0%, #79ace9 100%);
		background-image: linear-gradient(to bottom, #bddef4 0%, #79ace9 100%);
		filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr="#bddef4", EndColorStr="#79ace9");
		outline: none;
	  }
  
	  button.btn:focus:after, div.DTE_Form_Buttons button:focus:after {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: white;
		display: block;
		content: " ";
		-webkit-animation-duration: 1s;
		-webkit-animation-name: buttonPulse;
		-webkit-animation-fill-mode: forwards;
		-webkit-animation-iteration-count: infinite;
		-webkit-animation-timing-function: linear;
		-webkit-animation-direction: alternate;
		-moz-animation-duration: 1s;
		-moz-animation-name: buttonPulse;
		-moz-animation-fill-mode: forwards;
		-moz-animation-iteration-count: infinite;
		-moz-animation-timing-function: linear;
		-moz-animation-direction: alternate;
		-o-animation-duration: 1s;
		-o-animation-name: buttonPulse;
		-o-animation-fill-mode: forwards;
		-o-animation-iteration-count: infinite;
		-o-animation-timing-function: linear;
		-o-animation-direction: alternate;
		animation-duration: 1s;
		animation-name: buttonPulse;
		animation-fill-mode: forwards;
		animation-iteration-count: infinite;
		animation-timing-function: linear;
		animation-direction: alternate;
	  }
  
	  &.DTE_Action_Remove div.DTE_Body_Content {
		text-align: center;
		padding: 20px 0;
	  }
	}
  
	&.DTTT_container {
	  float: left;
	}
  
	&.DTE_Field {
	  input, textarea {
		box-sizing: border-box;
		background-color: white;
		-webkit-transition: background-color ease-in-out .15s;
		transition: background-color ease-in-out 0.15s;
	  }
  
	  input:focus, textarea:focus {
		background-color: #ffe;
	  }
  
	  input {
		&[type=color], &[type=date], &[type=datetime], &[type=datetime-local], &[type=email], &[type=month], &[type=number], &[type=password], &[type=search], &[type=tel], &[type=text], &[type=time], &[type=url], &[type=week] {
		  padding: 5px 4px;
		  width: 100%;
		  border: 1px solid #aaa;
		  border-radius: 3px;
		}
  
		&[type=checkbox], &[type=radio] {
		  vertical-align: middle;
		}
	  }
  
	  select {
		padding: 4px;
		border-radius: 3px;
		border: 1px solid #aaa;
		background: white;
	  }
  
	  label div.DTE_Label_Info {
		font-size: .85em;
		margin-top: 0.25em;
  
		&:empty {
		  margin-top: 0;
		}
	  }
  
	  div {
		&.DTE_Field_Info, &.DTE_Field_Message, &.DTE_Field_Error {
		  font-size: 11px;
		  line-height: 1em;
		  margin-top: 5px;
		}
  
		&.DTE_Field_Info:empty, &.DTE_Field_Message:empty {
		  margin-top: 0;
		}
  
		&.DTE_Field_Error {
		  &:empty {
			margin-top: 0;
		  }
  
		  display: none;
		  color: #b11f1f;
		}
  
		&.multi-value {
		  display: none;
		  border: 1px dotted #666;
		  border-radius: 3px;
		  padding: 5px;
		  background-color: #fafafa;
		  cursor: pointer;
  
		  span {
			font-size: .8em;
			line-height: 1.25em;
			display: block;
			color: #666;
		  }
  
		  &.multi-noEdit {
			border: 1px solid #ccc;
			cursor: auto;
			background-color: #fcfcfc;
  
			&:hover {
			  background-color: #fcfcfc;
			}
		  }
  
		  &:hover {
			background-color: #f1f1f1;
		  }
		}
	  }
  
	  &.disabled {
		color: grey;
  
		div.multi-value {
		  cursor: default;
		  border: 1px dotted #aaa;
		  background-color: transparent;
		}
	  }
  
	  div.multi-restore {
		display: none;
		margin-top: .5em;
		font-size: .8em;
		line-height: 1.25em;
		color: #3879d9;
  
		&:hover {
		  text-decoration: underline;
		  cursor: pointer;
		}
	  }
	}
  
	&.DTE_Field_Type_textarea textarea {
	  padding: 3px;
	  width: 100%;
	  height: 80px;
	  border: 1px solid #aaa;
	}
  
	&.DTE_Field.DTE_Field_Type_date img {
	  vertical-align: middle;
	  cursor: pointer;
	}
  
	&.DTE_Field_Type_checkbox div.DTE_Field_Input > div > div, &.DTE_Field_Type_radio div.DTE_Field_Input > div > div {
	  margin-bottom: 0.25em;
	}
  
	&.DTE_Field_Type_checkbox div.DTE_Field_Input > div > div:last-child, &.DTE_Field_Type_radio div.DTE_Field_Input > div > div:last-child {
	  margin-bottom: 0;
	}
  
	&.DTE_Field_Type_checkbox div.DTE_Field_Input > div > div label, &.DTE_Field_Type_radio div.DTE_Field_Input > div > div label {
	  margin-left: .75em;
	  vertical-align: middle;
	}
  
	&.DTE_Field_Type_select div.DTE_Field_Input {
	  padding-top: 4px;
	}
  
	&.DTE_Field_Type_datatable {
	  div {
		&.dataTables_info {
		  font-size: .8em;
		  line-height: 1.3em;
		  padding-right: 1em;
		  padding-bottom: 0.5em;
  
		  span.select-info {
			display: block;
		  }
		}
  
		&.dataTables_filter {
		  padding-right: 1em;
		  padding-bottom: 0.5em;
  
		  input {
			width: 100%;
		  }
		}
  
		&.dt-buttons {
		  padding-right: 1em;
		  padding-bottom: 0.5em;
  
		  button {
			width: 100%;
		  }
  
		  &:empty {
			padding-bottom: 0;
		  }
		}
  
		&.dataTables_paginate {
		  float: none;
		  text-align: center;
		  font-size: 0.8em;
		}
	  }
  
	  table.dataTable {
		&.no-footer {
		  border-bottom: 1px solid #aaa !important;
		}
  
		thead {
		  th, td {
			font-weight: normal;
			padding-top: 3px;
			padding-bottom: 3px;
			border-bottom: 1px solid #aaa;
		  }
		}
  
		tbody {
		  th:first-child, td:first-child {
			border-left: 1px solid #aaa;
		  }
  
		  th:last-child, td:last-child {
			border-right: 1px solid #aaa;
		  }
		}
  
		tfoot {
		  th, td {
			font-weight: normal;
			padding-top: 3px;
			padding-bottom: 3px;
			border-top: 1px solid #aaa;
		  }
		}
	  }
  
	  div {
		&.dataTables_scrollHead table.dataTable {
		  border-bottom: none !important;
  
		  thead {
			th, td {
			  border-bottom: none !important;
			}
		  }
		}
  
		&.dataTables_scrollBody {
		  border: 1px solid #aaa !important;
		  border-radius: 3px;
  
		  table.dataTable {
			border-bottom: none !important;
  
			tbody {
			  th:first-child, td:first-child {
				border-left: none;
			  }
  
			  th:last-child, td:last-child {
				border-right: none;
			  }
			}
		  }
		}
	  }
	}
  
	&.DTE_Body {
	  padding: 52px 0;
  
	  div.DTE_Body_Content {
		position: relative;
		overflow: auto;
  
		div {
		  &.DTE_Form_Info {
			padding: 1em 1em 0 1em;
			margin: 0;
		  }
  
		  &.DTE_Field {
			position: relative;
			clear: both;
			padding: 5px 20%;
			border: 1px solid transparent;
  
			&:after {
			  display: block;
			  content: ".";
			  height: 0;
			  line-height: 0;
			  clear: both;
			  visibility: hidden;
			}
  
			&:hover {
			  background-color: #f9f9f9;
			  border: 1px solid #f3f3f3;
			}
  
			> {
			  label {
				float: left;
				width: 40%;
				padding-top: 6px;
			  }
  
			  div.DTE_Field_Input {
				float: right;
				width: 60%;
			  }
			}
  
			&.full {
			  padding: 5px 0 5px 20%;
  
			  > {
				label {
				  width: 30%;
				}
  
				div.DTE_Field_Input {
				  width: 70%;
				}
			  }
			}
  
			&.block > div.DTE_Field_Input {
			  float: none;
			  clear: both;
			  width: 100%;
			}
		  }
		}
	  }
	}
  }
  
  @-webkit-keyframes buttonPulse {
	0% {
	  opacity: 0;
	}
  
	100% {
	  opacity: 0.2;
	}
  }
  
  @-moz-keyframes buttonPulse {
	0% {
	  opacity: 0;
	}
  
	100% {
	  opacity: 0.2;
	}
  }
  
  @-o-keyframes buttonPulse {
	0% {
	  opacity: 0;
	}
  
	100% {
	  opacity: 0.2;
	}
  }
  
  @keyframes buttonPulse {
	0% {
	  opacity: 0;
	}
  
	100% {
	  opacity: 0.2;
	}
  }
  
  html[dir=rtl] div {
	&.DTE_Body div.DTE_Body_Content div.DTE_Field > {
	  label {
		float: right;
	  }
  
	  div.DTE_Field_Input {
		float: left;
	  }
	}
  
	&.DTE div.DTE_Form_Buttons button {
	  float: left;
	}
  }
  
  @media only screen and (max-width: 768px) {
	div.DTE_Body div.DTE_Body_Content div.DTE_Field {
	  padding: 5px 10%;
  
	  &.full {
		padding: 5px 0 5px 10%;
  
		> {
		  label {
			width: 35.5%;
		  }
  
		  div.DTE_Field_Input {
			width: 64.5%;
		  }
		}
	  }
  
	  &.block > div.DTE_Field_Input {
		width: 100%;
	  }
	}
  }
  
  @media only screen and (max-width: 640px) {
	div.DTE_Body div.DTE_Body_Content div.DTE_Field {
	  padding: 5px 0;
  
	  &.full {
		padding: 5px 0%;
  
		> {
		  label {
			width: 40%;
		  }
  
		  div.DTE_Field_Input {
			width: 60%;
		  }
		}
	  }
  
	  &.block > div.DTE_Field_Input {
		width: 100%;
	  }
	}
  }
  
  @media only screen and (max-width: 580px) {
	div.DTE_Body div.DTE_Body_Content div.DTE_Field {
	  position: relative;
	  clear: both;
	  padding: 5px 0;
  
	  > {
		label {
		  float: none;
		  width: auto;
		  padding-top: 0;
		}
  
		div.DTE_Field_Input {
		  float: none;
		  width: auto;
		}
	  }
  
	  &.full, &.block {
		padding: 5px 0;
	  }
  
	  &.full > {
		label, div.DTE_Field_Input {
		  width: 100%;
		}
	  }
  
	  &.block > {
		label, div.DTE_Field_Input {
		  width: 100%;
		}
	  }
	}
  }
  
  div {
	&.DTE_Bubble {
	  position: absolute;
	  z-index: 11;
	  margin-top: -6px;
	  opacity: 0;
  
	  div {
		&.DTE_Bubble_Liner {
		  position: absolute;
		  bottom: 0;
		  border: 1px solid black;
		  width: 300px;
		  margin-left: -150px;
		  background-color: white;
		  box-shadow: 0 12px 30px 0 $black-5;
		  border-radius: 6px;
		  border: 1px solid #666;
		  padding: 1em;
		  background: #fcfcfc;
		  -webkit-box-sizing: border-box;
		  -moz-box-sizing: border-box;
		  box-sizing: border-box;
  
		  div {
			&.DTE_Bubble_Table {
			  width: 100%;
  
			  > form div.DTE_Form_Content {
				padding: 0;
  
				div.DTE_Field {
				  position: relative;
				  margin-bottom: 0.5em;
  
				  &:last-child {
					margin-bottom: 0;
				  }
  
				  > {
					label {
					  padding-top: 0;
					  margin-bottom: 0;
					}
  
					div {
					  padding: 0;
  
					  input {
						margin: 0;
					  }
					}
				  }
				}
			  }
  
			  div.DTE_Form_Buttons {
				text-align: right;
				margin-top: 1em;
  
				button {
				  margin-bottom: 0;
				}
			  }
			}
  
			&.DTE_Header {
			  border-top-left-radius: 5px;
			  border-top-right-radius: 5px;
  
			  + div {
				&.DTE_Form_Info, &.DTE_Bubble_Table {
				  padding-top: 42px;
				}
			  }
			}
  
			&.DTE_Form_Error {
			  float: none;
			  display: none;
			  padding: 0;
			  margin-bottom: 0.5em;
			}
  
			&.DTE_Bubble_Close {
			  position: absolute;
			  top: 19px;
			  right: 19px;
			  cursor: pointer;
			  z-index: 12;
  
			  &:after {
				content: "";
				color: $black-5;
				font-weight: bold;
				font-size: 22px;
				font-family: "Courier New", Courier, monospace;
			  }
  
			  &:hover:after {
				color: black;
			  }
			}
		  }
		}
  
		&.DTE_Bubble_Triangle {
		  position: absolute;
		  height: 10px;
		  width: 10px;
		  top: -6px;
		  background-color: white;
		  border: 1px solid #666;
		  border-top: none;
		  border-right: none;
		  -webkit-transform: rotate(-45deg);
		  -moz-transform: rotate(-45deg);
		  -ms-transform: rotate(-45deg);
		  -o-transform: rotate(-45deg);
		  transform: rotate(-45deg);
		}
	  }
  
	  &.below div {
		&.DTE_Bubble_Liner {
		  top: 10px;
		  bottom: auto;
		}
  
		&.DTE_Bubble_Triangle {
		  top: 4px;
		  -webkit-transform: rotate(135deg);
		  -moz-transform: rotate(135deg);
		  -ms-transform: rotate(135deg);
		  -o-transform: rotate(135deg);
		  transform: rotate(135deg);
		}
	  }
	}
  
	&.DTE_Bubble_Background {
	  position: fixed;
	  top: 0;
	  left: 0;
	  width: 100%;
	  height: 100%;
	  background: $black-7;
	  background: -ms-radial-gradient(center, ellipse farthest-corner, $black-3 0%, $black-7 100%);
	  background: -moz-radial-gradient(center, ellipse farthest-corner, $black-3 0%, $black-7 100%);
	  background: -o-radial-gradient(center, ellipse farthest-corner, $black-3 0%, $black-7 100%);
	  background: -webkit-gradient(radial, center center, 0, center center, 497, color-stop(0, $black-3), color-stop(1, $black-7));
	  background: -webkit-radial-gradient(center, ellipse farthest-corner, $black-3 0%, $black-7 100%);
	  background: radial-gradient(ellipse farthest-corner at center, $black-3 0%, $black-7 100%);
	  z-index: 10;
  
	  > div {
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
		-ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000)";
  
		&:not([dummy]) {
		  filter: progid:DXImageTransform.Microsoft.gradient(enabled="false");
		}
	  }
	}
  
	&.DTE_Inline {
	  position: relative;
	  display: table;
	  width: 100%;
  
	  div {
		&.DTE_Inline_Field, &.DTE_Inline_Buttons {
		  display: table-cell;
		  vertical-align: middle;
		}
  
		&.DTE_Inline_Field div.DTE_Field, &.DTE_Inline_Buttons div.DTE_Field {
		  padding: 0;
		}
  
		&.DTE_Inline_Field div.DTE_Field > label, &.DTE_Inline_Buttons div.DTE_Field > label {
		  display: none;
		}
  
		&.DTE_Inline_Field div.DTE_Field input {
		  &[type=color], &[type=date], &[type=datetime], &[type=datetime-local], &[type=email], &[type=month], &[type=number], &[type=password], &[type=search], &[type=tel], &[type=text], &[type=time], &[type=url], &[type=week] {
			width: 100%;
		  }
		}
  
		&.DTE_Inline_Buttons div.DTE_Field input {
		  &[type=color], &[type=date], &[type=datetime], &[type=datetime-local], &[type=email], &[type=month], &[type=number], &[type=password], &[type=search], &[type=tel], &[type=text], &[type=time], &[type=url], &[type=week] {
			width: 100%;
		  }
		}
  
		&.DTE_Inline_Field div.DTE_Form_Buttons button, &.DTE_Inline_Buttons div.DTE_Form_Buttons button {
		  margin: -6px 0 -6px 4px;
		  padding: 5px;
		}
  
		&.DTE_Field input {
