		  line-height: 1em;
		  position: absolute;
		  top: 0;
		  left: 0;
		  width: 100%;
		  opacity: 0;
		}
  
		div {
		  &.drop {
			position: relative;
			box-sizing: border-box;
			width: 100%;
			height: 100%;
			border: 3px dashed #ccc;
			border-radius: 6px;
			min-height: 4em;
			color: #999;
			padding-top: 3px;
