			}
  
			span {
			  max-width: 75%;
			  font-size: .85em;
			  line-height: 1em;
			}
		  }
  
		  &.rendered img {
			max-width: 8em;
			margin: 0 auto;
		  }
		}
  
		&.noDrop div {
		  &.drop, &.row.second {
			display: none;
		  }
  
		  &.rendered {
			margin-top: 10px;
		  }
		}
  
		&.noClear div.clearValue button {
		  display: none;
		}
  
		&.multi {
		  div {
			&.cell {
			  display: block;
			  width: 100%;
  
			  div.drop {
				min-height: 0;
				padding-bottom: 5px;
			  }
			}
  
			&.clearValue {
			  display: none;
			}
		  }
  
		  ul {
			list-style-type: none;
