			li {
			  position: relative;
			  margin-top: 0.5em;
  
			  &:first-child {
				margin-top: 0;
			  }
  
			  img {
				vertical-align: middle;
			  }
  
			  button {
				position: absolute;
				width: 40px;
				right: 0;
				top: 50%;
				margin-top: -1.5em;
			  }
			}
		  }
		}
	  }
  
	  &.DTE_Processing_Indicator {
		position: absolute;
		top: 17px;
		right: 42px;
		height: 2em;
		width: 2em;
		z-index: 20;
		font-size: 12px;
		display: none;
		-webkit-transform: translateZ(0);
		-ms-transform: translateZ(0);
		transform: translateZ(0);
	  }
	}
  
	&.processing div {
	  &.DTE_Processing_Indicator {
		display: block;
	  }
  
	  &.DTE_Field div.DTE_Processing_Indicator {
		display: none;
	  }
	}
  
	div.DTE_Field div.DTE_Processing_Indicator {
	  top: 13px;
	  right: 0;
	  font-size: 8px;
	}
  
	&.DTE_Inline div.DTE_Processing_Indicator {
	  top: 5px;
	  right: 6px;
	  font-size: 6px;
	}
  
	&.DTE_Bubble div.DTE_Processing_Indicator {
	  top: 10px;
	  right: 14px;
	  font-size: 8px;
	}
  
	div.DTE_Processing_Indicator {
	  span, &:before, &:after {
		display: block;
		background: black;
		width: .5em;
		height: 1.5em;
		border: 1px solid $black-4;
		background-color: $black-1;
		-webkit-animation: editorProcessing .9s infinite ease-in-out;
		animation: editorProcessing 0.9s infinite ease-in-out;
	  }
  
	  &:before, &:after {
		position: absolute;
		top: 0;
		content: "";
	  }
  
	  &:before {
		left: -1em;
		-webkit-animation-delay: -0.3s;
		animation-delay: -0.3s;
	  }
  
	  span {
		-webkit-animation-delay: -0.15s;
		animation-delay: -0.15s;
	  }
  
	  &:after {
		left: 1em;
	  }
	}
  }
  
  @-webkit-keyframes editorProcessing {
	0%, 80%, 100% {
	  transform: scale(1, 1);
	}
  
	40% {
	  transform: scale(1, 1.5);
	}
  }
  
  @keyframes editorProcessing {
	0%, 80%, 100% {
	  transform: scale(1, 1);
	}
  
	40% {
	  transform: scale(1, 1.5);
	}
  }