.intl-tel-input {
    position: relative;
    border-color: rgba(215, 227, 243, 0.5);
  
    .hide {
      display: none;
    }
  
    .flag-dropdown {
      position: absolute;
      z-index: 1;
      cursor: pointer;
      height: 40px;
      line-height: 10px;
  
      .selected-flag {
        margin: 1px 2px 1px 1px;
        padding: 11px 16px 10px 6px;
        background: $background;
        border-bottom-left-radius: 11px;
        border-top-left-radius: 11px;
  
        &:hover {
          background-color: #eaeef9;
        }
      }
    }
  }
  
  .country-list .highlight {
    float: inherit;
  }
  
  .intl-tel-input {
    .flag-dropdown {
      .selected-flag .down-arrow {
        top: 6px;
        position: relative;
        left: 20px;
        width: 0;
        height: 0;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-top: 4px solid black;
      }
  
      .country-list {
        list-style: none;
        padding: 0;
        margin: 0;
        z-index: 1;
        overflow-y: scroll;
        box-shadow: 1px 1px 4px rgb(209, 212, 231);
        background-color: white;
        border: 0;
        position: absolute;
        top: 42px;
        width: 250px;
        max-height: 200px;
        box-shadow: 0px 12px 41px 0px rgba(108, 119, 138, 0.3);
  
        .divider {
          padding-bottom: 5px;
          margin-bottom: 5px;
          border-bottom: 1px solid #eaf0ff;
        }
  
        .country {
          line-height: 16px;
          padding: 10px 10px;
          font-size: 13px;
  
          .dial-code {
            color: #9097b5;
          }
  
          &.highlight {
            background-color: rgb(238, 242, 253);
            font-size: 13px;
            border: 0;
            border-radius: 0;
            margin: 0;
          }
        }
  
        .flag {
          display: inline-block;
          vertical-align: bottom;
          margin-right: 6px;
        }
  
        .country-name {
          margin-right: 6px;
        }
      }
    }
  
    input {
      box-sizing: border-box;
      -moz-box-sizing: border-box;
      height: 39px;
      padding-left: 47px;
      position: relative;
      z-index: 0;
      border: 1px solid #e8e8f7;
      width: 250px;
      border-bottom-left-radius: 11px;
      border-top-left-radius: 11px;
  
      &:focus {
        outline: none;
      }
    }
  }
  
